
import * as React from "react";
  
// importing material UI components
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import logo from '../../assets/img/logo1.png'
import { useMediaQuery } from 'react-responsive'

 const Header =(props)=> {
  const {OurPromiseRef,SustainabilityRef,contactUsRef} =props
  const executeScrollOurPromise = () => OurPromiseRef.current.scrollIntoView({ behavior: "smooth" });  
  const executeScrollOurSustainability = () => SustainabilityRef.current.scrollIntoView({ behavior: "smooth" });  
  const executeScrollcontactUs = () => contactUsRef.current.scrollIntoView({ behavior: "smooth" });  
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })
  return (
      <AppBar  style={{ background:'white' }}  position='sticky'>
        <Toolbar style={{height:'8vh'}} >
          {/*Inside the IconButton, we 
           can render various icons*/}
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            {/*This is a simple Menu 
             Icon wrapped in Icon */}
            {/* <MenuIcon /> */}
          </IconButton>
          {/* The Typography component applies 
           default font weights and sizes */}
  
          <Typography variant="h6" 
            style={{ color: 'grey',verticalAlign:'middle' }} 
            component="div" sx={{ flexGrow: 1 }}>
           <img  src={logo} />
          </Typography>
          <div style={{marginRight:'10px'}}>
          
        {isTabletOrMobile?null:  <>
          <Button color="inherit"  onClick={()=>{executeScrollOurPromise()}} style={{ color: 'grey',textTransform:'none' }} > Our Promise </Button>
          <Button color="inherit" onClick={()=>{executeScrollOurSustainability()}}  style={{ color: 'grey',textTransform:'none' }} >Sustaibility</Button>
          <Button color="inherit"  onClick={()=>{executeScrollcontactUs()}} style={{ color: 'grey' ,textTransform:'none'}} >Get Sarted</Button>
          
          </>}
         <Button color="inherit"  onClick={()=>{executeScrollcontactUs()}} style={{ color: 'grey',textTransform:'none' }} >Contact Us</Button>
     
          </div>
               <Button    href="https://main.agricom.app" style={{ color: 'white' ,backgroundColor:'#5fb47c'}} > Sign In</Button>
        </Toolbar>
      </AppBar>
  );
}
export default Header